import styled from "styled-components";
import { Link, useLocation } from "react-router-dom";

export default function Header({ isLight, handleToggleTheme }) {
	const location = useLocation();

	return (
		<Wrapper>
			<h1>My App Music</h1>
			<nav>
				<Link to="/">
					<MenuEl isCurrentPage={location.pathname === "/"}>Home</MenuEl>
				</Link>
				<Link to="/about">
					<MenuEl isCurrentPage={location.pathname === "/about"}>About</MenuEl>
				</Link>
			</nav>
			<Btn>
				<button onClick={handleToggleTheme}>
					{isLight ? "dark" : "light"}
				</button>
			</Btn>
		</Wrapper>
	);
}

const Wrapper = styled.header`
	display: flex;
	justify-content: space-between;
	font-size: 8rem;
	padding: 0 24px;
	height: 80px;
	align-items: center;
	border-bottom: solid 1px;
	& a {
		text-decoration: none;
		color: inherit;
	}
	& a:first-child {
		margin-right: 10%;
	}
`;

const MenuEl = styled.p`
	padding-bottom: 2px;
	border-bottom: solid 2px
		${(props) => (props.isCurrentPage ? "" : "transparent")};
	&:hover {
		border-bottom: solid 2px;
		transition: 0.25s;
	}
`;

const Btn = styled.div`
	display: flex;
	align-items: center;
	justify-content: end;
	height: 80px;
	width: 20%;
`;
