import { useState, useEffect, useRef } from "react";
import * as Tone from "tone";

// sounds importer
import loops1 from "assets/sounds/loops1.wav";
import loops2 from "assets/sounds/loops2.wav";
import loops3 from "assets/sounds/loops3.wav";
import loops4 from "assets/sounds/loops4.wav";

// fonction pour relier le Hook avec les sons
export default function useSounds() {
	const mySampler = useRef(null);

	// state animation visuel quand on joue le sons
	const [isLoops1Played, isLoops1PlayedChange] = useState(false);
	const [isLoops2Played, isLoops2PlayedChange] = useState(false);
	const [isLoops3Played, isLoops3PlayedChange] = useState(false);
	const [isLoops4Played, isLoops4PlayedChange] = useState(false);

	useEffect(() => {
		const sampler = new Tone.Sampler({
			C4: loops1,
			"D#4": loops2,
			"F#4": loops3,
			A4: loops4,
		}).toDestination();

		Tone.loaded().then(() => {
			mySampler.current = sampler;
		});
	}, []);

	// fonction pour jouer les sounds
	function soundPlay(note) {
		mySampler.current.triggerAttackRelease([note], 4);
	}

	// Fonction pour ajouter un fichier de musique
	function handleSampleChange(note, file) {
		let fileURL = URL.createObjectURL(file);
		let buffer = new Tone.Buffer(fileURL);
		mySampler.current.add(note, buffer, () =>
			alert("Sample successfully changed"),
		);
	}

	// Liste des sons par bouton
	const buttonsList = [
		{
			soundPlay: () => soundPlay("C4"),
			isPlayed: isLoops1Played,
			id: "Loops1",
			handleSampleChange: (e) => handleSampleChange("C4", e.target.files[0]),
		},
		{
			soundPlay: () => soundPlay("D#4"),
			isPlayed: isLoops2Played,
			id: "Loops2",
			handleSampleChange: (e) => handleSampleChange("D#4", e.target.files[0]),
		},
		{
			soundPlay: () => soundPlay("F#4"),
			isPlayed: isLoops3Played,
			id: "Loops3",
			handleSampleChange: (e) => handleSampleChange("F#4", e.target.files[0]),
		},
		{
			soundPlay: () => soundPlay("A4"),
			isPlayed: isLoops4Played,
			id: "Loops4",
			handleSampleChange: (e) => handleSampleChange("A4", e.target.files[0]),
		},
	];

	// fonction pour jouer les notes au clavier
	function handleKeyDown({ key }) {
		switch (key) {
			case "a":
				isLoops1PlayedChange(true);
				window.setTimeout(() => isLoops1PlayedChange(false), 300);
				soundPlay("C4");
				break;
			case "z":
				isLoops2PlayedChange(true);
				window.setTimeout(() => isLoops2PlayedChange(false), 300);
				soundPlay("D#4");
				break;
			case "e":
				isLoops3PlayedChange(true);
				window.setTimeout(() => isLoops3PlayedChange(false), 300);
				soundPlay("F#4");
				break;
			case "r":
				isLoops4PlayedChange(true);
				window.setTimeout(() => isLoops4PlayedChange(false), 300);
				soundPlay("A4");
				break;
			default:
				break;
		}
	}

	useEffect(() => {
		window.addEventListener("keydown", handleKeyDown);

		return () => {
			window.removeEventListener("keydown", handleKeyDown);
		};
	});

	return { buttonsList };
}
